"use strict";

const Map = {};

// 
Map.required = (val) => {
	return val !== '';
}
// 
Map.number = (val) => {
	return /^\d*$/.test(val);
}
// 
Map.min = (val, num) => {
	return val === '' || replaceEnter(val).length >= num;
}
// 
Map.max = (val, num) => {
	return val === '' || replaceEnter(val).length <= num;
}

// IE8 textarea \r\n
const REX_ENTER = /\r/g; 
function replaceEnter(val){
	return val.replace(REX_ENTER, '');
};

export default Map;



















